html
{
  display: flex;
  font-size: 18px;
}

@media (max-width: 768px)
{
  html { font-size: 16px }
}

body
{
  font-family: "Source Sans Pro", Arial, sans-serif;
  font-weight: 300;
  margin: 0;
  /* Start below fixed navbar */
  padding-top: 4em;
  line-height: 1.5;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
}

h1,
.h1,
h2,
h3,
h4,
h5 { font-weight: 700;}

.button { font-weight: 400; }

#content,
#blog
{
  padding-bottom: 2em;
  flex-grow: 1;
}

a img
{
  border: none;
}

a:link, a:visited
{
  color: #555;
}

button
{
  cursor: pointer;
}

code, pre
{
  font-family: monospace;
}

pre
{
  background: #ececec;
  padding: 1em;
  overflow: auto;
}

.sprite
{
  display: inline-block;
  font-size: 0px; /* fix for IE6 height bug */
  background-image: url(../../img/sprite-main.png);
  background-repeat: no-repeat;
}

.toc
{
  display: inline-block;
  border: 1px solid #D2D2D2;
  background: #fff;
  padding: 16px 20px;
  border-radius: 4px;
}

.toc ul
{
  list-style: none;
  margin: 0;
  padding: 0;
}

.toc ul ul { padding-left: 40px; }

.toc li { margin: 4px 0; }

.toc.toc-list { padding: 1em; }

.toc.toc-list p
{
  margin-top: 0;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  color: #585858;
}

.toc.toc-list ul { padding-left: 1em; }

.toc.toc-list ul li
{
  font-size: 16px;
  line-height: 2em;
}

.toc.toc-list ul li a.active
{
  color: #000;
  font-weight: bolder;
  position: relative;
}

.toc.toc-list ul li .active:before
{
  content: "";
